.footer{
    display: flex;
    background-color: #8d52b9;
    flex-direction: row;
    width: 100%;
    padding: 1%;
}

.contacts{
    display: flex;
    flex-direction: row;
    float: left;
    width: 50%;
    margin-left: 5%;
    justify-content: center;
    align-items: center;

}
.mail{
    margin-top: 3px;
    margin-left: 5px;
    display: flex;
    float: right;
    font-size: larger;
    font-weight: bolder;
}
@media (max-width: 800px) {
    .footer{
        flex-direction: column;
        width: 98%;
    }
    .contacts{
        margin-left: 25%;
    }
    .social{
        margin-left: 25%;
    }
    .mailIcon{
        visibility: hidden;
    }
  }
.social{
    display: flex;
    flex-direction: row;
    float: right;
    width: 50%;
    margin-right: 0%;
    justify-content: center;
}

a:link {
    text-decoration: none;
    color: inherit;
  }
  
a:visited {
    text-decoration: none;
    color: inherit;
  }
  
a:hover {
    text-decoration: none;
    color: inherit;
  }
  
a:active {
    text-decoration: none;
    color: inherit;
  }