.skills{
 margin-left: 10%;
 margin-top: 10%;
 margin-right: 10%;
 margin-bottom: 0%;
}
.skills-heading{
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: xx-large;
    font-weight: lighter;
    margin-bottom: 5%;
}
.skills-box{
    background: #2a0744;
    margin-left: 5%;
    margin-right: 5%;
    border: 2px solid #835ad4;
    border-radius: 20px;
    transition-duration: 500ms;
    padding: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media (max-width: 800px) {
    .skills-box {
      flex-direction: column;
    }
  }
.skills-box:hover{
    background: #4e0f7e;
    margin-top: 2%;
    margin-left: 10%;
    margin-right: 10%;
}
.skills-box-skillname{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 0%;
    margin-bottom: 0%;
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: large;
    float:left;
    cursor: pointer;
    transition-duration: 500ms;
    
}
.skills-box-skillname:hover{
    color: #8f66e9;
}