
.timeline{
    margin-left: 10%;
    margin-top: 10%;
}
.timeline-heading{
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: xx-large;
    font-weight: lighter;
    margin-bottom: 0%;
}

.timeline-entry{
    width: 90%;
    display: flex;
    margin-left: 0%;
    position: relative;
    left: 4.7%;
    padding-bottom: 10%;
}
.timeline-entry-left{
    width: 20%;
    display: flex;
    float: left;
    justify-content: center;
    align-items: center;
    margin-left: 0%;
    margin-right: 5%;
    text-align: center;
}
.timeline-entry-right{
    width: 80%;
    display: flex;
    float: right;
    
    
}
.timeline-entry-date{
    color: #B39DDF;
    font-size: small;
    font-family: 'Roboto', sans-serif;
    margin-left: 40%;
    
}

.timeline-entry-para{
    width: 70%;
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: x-large;
    padding: 2%;
    border: 2px solid #835ad4;
    border-radius: 20px;
    background-color: #2a0744;
    padding-left: 5%;
    padding-right: 5%;
}
