.Project{
    border: 2px solid #835ad4;
    border-radius: 20px;
    width: 90%;
    margin-left: 5%;
    margin-bottom: 5%;
    display: flex;
    background: #2a0744;
    font-family: 'Roboto', sans-serif;
}
@media (max-width: 800px) {
    .Project {
      flex-direction: column;
      text-align: center;
      align-items: center;
      justify-content: center;
    }
  }
.left{
    width: 20%;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #B39DDF;
}
.centre{
    width: 30%;
    display: flex;
    justify-content: left;
    align-items: center;
    color: rgb(230, 221, 221);
    
}
@media (max-width: 800px) {
    .centre {
      flex-direction: column;
    }
  }
.heading{
    font-weight: lighter;
}
.right{
    width: 50%;
    float: right;
}
.technologies{
    margin-top: 3%;
    margin-bottom: 0%;
    text-align: center;
    color: #835ad4;
}
.technology{
    text-align: center;
    font-weight: bold;
    font-size: large;
}
.description{
    color: rgb(230, 221, 221);
    font-family: 'Roboto', sans-serif;
    color: white;
    font-size: x-large;
}